.flashcard {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    transform-style: preserve-3d;
    transition: 150ms;
    transform: perspective(1000px) rotateY(0.1deg) rotateY(var(--rotate-y, 0));
}

.flashcard.flip {
    --rotate-y: 180deg;
}

.flashcard-front,
.flashcard-back {
    position: absolute;
    backface-visibility: hidden;
    height: 100%;
    width: 100%;
}

.flashcard-back {
    transform: rotateY(180deg);
}
